import React, { ReactElement } from 'react';

interface BottomProps {
	name: ReactElement | string
	hide?: boolean
	onClick?: () => void
	icon?: JSX.Element
	className?: string
}
export function Bottom(arr: BottomProps[]): ReactElement[] {
	return arr.map((item, i) => {
		if (!item) return null;
		let {name, icon, onClick, hide} = item;
		if (hide) return null;
		return <div key={['button', i].join('.')} onClick={onClick} className={['button', item.className].join(' ')}>
			{icon}{' '}{name}
		</div>;
	});
}